//
// Variables
// --------------------------------------------------

// Fonts
// -------------------

$headingFont: 'Times New Roman', sans-serif;
$paragraphFont: 'Garamond', sans-serif;

// Colours
// -------------------
$darkPrimaryColor: #512DA8;
$lightPrimaryColor: #D1C4E9;
$primaryColor: #673AB7;
$textIconColor: #FFFFFF;
$accentColor: #009688;
$primaryTextColor: #212121;
$secondaryTextColor: #757575;
$dividerColor: #BDBDBD;

$baseColor: #EEEEEE;

$socialFacebook: #3B5998;
$socialTwitter: #00ACED;
$socialInstagram: #3F729B;
$socialYoutube: #CD201F;

$errorColor: #AA0000;
$successColor: #00AA00;
$baseFontSize: 16;

// Breakpoints
// -------------------

$breakpoints: (
	'mobile': (max-width: 800px),
	'tablet': (max-width: 1024px),
	'desktop': (min-width: 1200px),
);

// Include gutter on outside 
$gutterOnOutside: true;

// Breakpoints Grid Settings
// -------------------

$grid-settings: (
	base: (
		container-columns: 12,
		gutter: 1%,
		max-width: 1100px,
	),
	desktop: (
		container-columns: 12,
		gutter: 1%,
		max-width: 1200px,
	),
	tablet: (
		container-columns: 8,
		gutter: 1%,
	),
	mobile: (
		container-columns: 1,
		gutter: 5%,
	)
);
