@import 'variables.scss';
@import 'mixins/_grid.scss';
@import 'mixins/_helpers.scss';

@import 'type.scss';
section.container.blog {
	article.title {	
		h4 {
			text-align: right;
			color: $secondaryTextColor;
		}
	}
	article.post {
		section {
			boder: none;
			padding: 1em 2em;
			line-height: 1.3em;
			h2 {
				margin-top: 1em;
			}
			.code, blockquote {
				@include font-size(16);
			}
			.code {
				width: 100%;
				padding: 1em 10px 0 10px;
  				resize: none;
				overflow-x: auto; 
				overflow-y: hidden;
				font-family: 'Courier', sans-serif;
			}
			blockquote {
				font-family: 'Courier new', sans-serif;
				margin: 1em;
				padding: 2em;
				.icon-quote {
					@include h1();
					float: left;
				}
			}
		
		}
		aside {
			border-width: 10px;
			border-color: $baseColor;
			border-style: solid;
			margin-left: 0.01px;
			padding: 1em 2em;
			a.category {
				padding: 5px;
				@include button-style($baseColor, $primaryColor);
				
			}
			a.tag {	
				padding: 5px;
				border-radius: 15px;
				@include button-style($baseColor, $accentColor);
			}
			&.author {
				position: relative;
				padding: 0;
  					vertical-align: middle;
				
				img {
					max-width:100px;
					height: 100px;
					max-height: 100px;
					border-radius: 0 5px 5px 0;
					background: $accentColor;					
				}
				div {
					margin: auto;
					padding: 10px;
					text-align: center;
					display: inline-block;
				}	
				&>* {
  					vertical-align: middle;
				}
			}
		}
	}
	article.comments {
		word-wrap: break-word;
		textarea {
			width: 100%;
			resize: vertical;
		}

		input[type="text"],input[type="email"] {
			width: 100%;
		}
		aside {
			float: right;
		}
	}
	article.paginator {
		background: $accentColor;
		color: $textIconColor;
		padding: 0px;
		border: none;
		section {
			text-align: center;
			padding: 0;	
			border: none;
		}
		span {
			display: inline-block;
			padding: 10px;
			background: darken($accentColor, 3%);
		}
		a {
			display: inline-block;
			padding: 10px;
			@include button-style($accentColor,$textIconColor);
		}
	}
}